@import url('https://fonts.googleapis.com/css?family=Lato:300|Raleway:300|Fira+Code&display=swap');

.App {
  text-align: center;
  background-color: #2c2c2c;
  display: table;
  align-content: center;
  justify-content: center;
  height:98.9vh;
  width:100%;
  margin:0px;
  overflow:hidden;
}

.App-main{
  display: table;
  align-content: center;
  justify-content: center;  
  position: relative;
  height:inherit;
  width:100%;  
  padding-bottom:10px;
}

.App-header {
  background:transparent;
  padding:5px;
  height:10%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Raleway';
  font-weight: lighter;
  font-size: calc(20px + 3vmin);
  color: white;
  background-color: #252525;
}

.App-body {
  background:transparent;
  height:calc(100% - 50px);
  margin:0px;
  display: flex;
  overflow:hidden;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Lato';
  font-weight: lighter;
  padding: 20px;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-content-table{
  width:calc(100px + 70vw);
}

.App-example{
  color:white;
}

.App-footer{
  font-family: 'Lato';
  font-size: calc(10px + 0.5vmin);
  color: white;
  background-color: #1b1b1b;
  position: absolute;  
  bottom: 0;
  width: 100%;
  height: 30px;   
  line-height: 30px;
  margin-left:0px;
  margin-top: 10px;
}

.App-link {
  color: #ff4e14;
  font-family: 'Raleway';
  font-weight: normal;
}

.App-proceed-button {
  margin:25px;
  width:200px;
  height:50px;
  border-radius:25px;
  border: 0px solid;
  font-family: 'Raleway';
  font-weight: bold;
  text-transform:uppercase;
  transition: border-radius 0.5s, width 0.5s;
  color: white;
  background-color:#ff4e14;
}

.App-proceed-button:focus{
  outline: none;  
  box-shadow: 0px 1px 5px #000000;
}

button:hover{
  border-radius:15px;
  width:220px;
}

.Input-qty{
  border:2px solid white;
  background-color: #414141;
  color:white;
  font-size: calc(10px + 2vmin);
  text-align:center;
  padding:5px;
  padding-left:20px;
  width:200px;
}
input:focus{
  outline: none;  
  box-shadow: 0px 2px 8px #000000;
}

.App-code-output{
  border: 2px solid #bdbdbd;
  border-radius: 15px;
  padding:10px;
  font-size: 14px;
  display:inline-block;
  text-align:left;
  font-family: 'Fira Code', monospace;
  color:#bdbdbd;
  background-color: #252525;
  width: 90%; 
  max-width:fit-content;
  min-width: none;
  overflow: auto; 
  
}

.App-explanation{
  font-size: 12px;
}

.App-unique-IDs{
  font-size: 20px;
  padding-top: 5px;
  padding-bottom: 5px;
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: #333333;
}
pre::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: transparent;
}
::-webkit-scrollbar-thumb:hover{
  background-color: #bbbbbb;
}
::-webkit-scrollbar-thumb {
  border-radius: 25px;
  background-color: #dbdbdb75;
}
::-webkit-scrollbar-track {
  border-radius: 25px;
  margin-left: 10px;
  margin-right: 10px;
  background-color: #5c5c5c75;
}
